@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body{
  font-family: 'Quicksand', sans-serif;
}

img{
  max-width: 100%;
}
body{background:#04093f;color:#fff}.head{font-size:18px;line-height:22px;text-decoration:underline}

.header{display:flex;justify-content:space-between}.logo{background:#5363f3;padding:0.5em 0 1em 0.5em;font-weight:bold;text-align:right;position:relative;text-transform:uppercase}.logo::after{position:absolute;content:"";height:100%;width:50px;top:0;margin-left:1em;transform:skew(-30deg);background:#5363f3;left:140px;z-index:-1}.logo__head{font-size:1.25rem;text-decoration:underline}.logo__subhead{font-size:0.5rem}.resume{background:#5363f3;min-width:4.375rem;-webkit-clip-path:polygon(100% 0, 0 0, 100% 100%);clip-path:polygon(100% 0, 0 0, 100% 100%)}

.content{padding:1em;margin-top:2rem}.profile{display:flex;justify-content:center}.profile__photo{margin-right:2.5rem}.profile__photo img{width:150px;height:150px;border:2.5px solid #3395ff;object-fit:cover;border-radius:50%}.profile__summary{max-width:450px;margin-right:2.5rem;padding:0.75em;background:#5363f3;border-radius:5px;border:2.5px solid #3395ff}.profile__summary p{font-size:14px;font-weight:bold;line-height:17px}.profile__technology{width:450px;padding:0.75em;background:#5363f3;border-radius:5px;border:2.5px solid #3395ff}.profile__technology .tech-container{width:100%;display:flex;justify-content:space-between;margin-top:1rem;padding:0 1rem}

.contact-container{display:flex;justify-content:center;width:100%}.contact{background:#5363f3;border:2.5px solid #3395ff;border-radius:5px;margin-right:2rem;padding:1em}.form{margin-top:1rem}.input{font-family:"Quicksand",sans-serif;border:none;min-height:28px;font-size:14px;margin-right:1rem;padding:0.5em;min-width:200px}.query{margin-top:1em;border:none;resize:none;font-family:"Quicksand",sans-serif;padding:1em}.button{border:none;background:#04093f;color:white;margin-top:1em;font-size:14px;padding:0.5em 2em;transition:all 200ms ease-in-out;font-family:"Quicksand",sans-serif;font-weight:500}.button:hover{opacity:0.9;cursor:pointer;transform:scale(1.05)}

.social{display:flex;flex-direction:column;justify-content:space-between}.social__icon{transition:all 100ms ease-in-out}.social__icon img{width:48px;height:48px}.social__icon:hover{transform:scale(1.2);cursor:pointer;background:#04093f}

